body {
	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
		'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: -webkit-gradient(linear, right top, left top, from(rgb(4, 5, 82)), to(rgba(0, 128, 128, 1)));
	background: -webkit-linear-gradient(right, rgb(4, 5, 82) 0%, rgba(0, 128, 128, 1) 100%);
	background: linear-gradient(to left, rgb(4, 5, 82) 0%, rgba(0, 128, 128, 1) 100%);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
